@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
/*       Fonts       */
/*       Colors       */
html {
  font-size: 16px;
}

/*      Main content        */
.module h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 2.625rem;
  color: #121212;
  line-height: 50.4px;
}
.module p {
  font-family: "Roboto", sans-serif;
  color: #121212;
  line-height: 24px;
}
.module .mainContent {
  display: flex;
  margin-top: 96px;
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
}
.module .mainContent .rightSide {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 95px;
  max-width: 380px;
}

/*      Alert       */
.module .alertBackground {
  display: none;
  position: absolute;
  background-color: rgba(18, 18, 18, 0.5);
  width: 100%;
  height: 100%;
  inset: 0;
  z-index: 1;
}
.module .alertBackground .alertContent {
  min-width: 40%;
  position: absolute;
  background-color: #FFFFFF;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 46px 46px 56px 95px;
}
.module .alertBackground .alertContent h1 {
  margin: 0;
}

/*      Buttons     */
.generalBtn {
  font-family: "Roboto", sans-serif;
  background-color: #E4C1AE;
  color: #FFFFFF;
  border-radius: 32px;
  border-width: 0px;
  width: 128px;
  height: 48px;
  font-weight: 700;
  font-size: 1rem;
  line-height: 16px;
}
.generalBtn:hover {
  outline: 1px, solid, #121212;
  color: #e6e6e6;
}
.generalBtn:active {
  background-color: #d9a78c;
  color: #bfbfbf;
  outline: 1px, solid, #121212;
}

.closeButton {
  color: #121212;
  font-size: 2rem;
  font-weight: 700;
  text-align: right;
  margin-block-start: 0;
  margin-block-end: 0;
  margin: 0;
}
.closeButton:hover {
  cursor: pointer;
}

#resetCountBtn {
  display: none;
}